@charset 'utf-8';
@font-face {
  font-family: "Proxima-Nova-Bold";
  src: url(data:font/opentype;base64,);
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: "Proxima-Nova-Semibold";
  src: url(data:font/opentype;base64,);
  font-style: normal;
  font-weight: 600;
}
@import url(https://fonts.googleapis.com/css?family=PT+Sans:400,700,400italic);
@import 'settings';
@import 'foundation';
@import 'motion-ui';
@import 'slick.scss';
@include foundation-global-styles;
@include foundation-grid;
// @include foundation-flex-grid;
@include foundation-typography;
@include foundation-button;
@include foundation-forms;
// @include foundation-range-input;
@include foundation-accordion;
@include foundation-accordion-menu;
@include foundation-badge;
@include foundation-breadcrumbs;
@include foundation-button-group;
@include foundation-callout;
@include foundation-close-button;
@include foundation-menu;
@include foundation-menu-icon;
@include foundation-drilldown-menu;
@include foundation-dropdown;
@include foundation-dropdown-menu;
@include foundation-flex-video;
@include foundation-label;
@include foundation-media-object;
@include foundation-off-canvas;
@include foundation-orbit;
@include foundation-pagination;
@include foundation-progress-bar;
// @include foundation-progress-element;
// @include foundation-meter-element;
@include foundation-slider;
@include foundation-sticky;
@include foundation-reveal;
@include foundation-switch;
@include foundation-table;
@include foundation-tabs;
@include foundation-thumbnail;
@include foundation-title-bar;
@include foundation-tooltip;
@include foundation-top-bar;
@include foundation-visibility-classes;
@include foundation-float-classes;
// @include foundation-flex-classes;

@include motion-ui-transitions;
@include motion-ui-animations;
body {
	font-size: 20px;


}
a.button,button {
	&:hover {
		background-color: map-get($foundation-palette,  success);
	}
}
.ba-intro {
background: url(../img/background-header.jpg) 50% no-repeat;
background-size: cover;
position: relative;
color: #FFF;
}
.ba-logo {
position: relative;
&__icon {
height: 73px;
width: 55px;
fill: #f6f6f6;
}
}
.ba-main-menu {
position: relative;
text-transform: uppercase;
li {
a {
	font-family: 'Proxima-Nova-Semibold', sans-serif ;
	font-size: 20px;
color: #FFF;
	&:hover {
		color: $secondary-color;
	}
}
}
}
.ba-intro:before {
position: absolute;
content: "";
left: 0;
top: 0;
bottom: 0;
right: 0;
z-index: 0;
background: rgba(159,154,207,.5);
}

.ba-intro-text {
position: relative;
text-align: center;
padding: 280px 0 500px;


&__info {
max-width: 660px;
margin: 0 auto;
font-family: 'PT Sans', sans-serif;
font-style: italic;
font-size: rem-calc(30px);
}
&__title {

text-transform: uppercase;
max-width: 970px;
margin: 0 auto;
font-family: 'Proxima-Nova-Bold', sans-serif;
}
}
.ba-about {
	padding-top: 125px;
	padding-bottom: 125px;
	color: $medium-gray;
}
.ba-about-container-left {
	background: url(assets/img/bg-about-us.jpg) 50% no-repeat;
	background-size: cover;
	color: #FFF;
	position: relative;
}
.ba-about__title {
	position: relative;
	z-index: 1;
	padding-top: 160px;
	padding-bottom: 345px;
	font-size: 36px;
	text-align: center;
	background: url(assets/img/icon=about-us.png) 50% 72px no-repeat;

}
.ba-about-container-left:before {
position: absolute;
content: "";
left: 0;
top: 0;
bottom: 0;
right: 0;
z-index: 0;
background: rgba(159,154,207,.5);
}
.fs-italic {
	font-style: italic;
}
.ba-about-container-right {
	 @include breakpoint(medium) {
    padding: 65px 0 85px 65px;
  }
}
.button {
	border: 1px solid $dark-gray;
	font-style: italic;
	text-align: center;
	padding-left: 45px;
	padding-right: 45px;
	font-size: 20px;
}
.text-center {
	text-align: center;
}

.ba-services {
	color: $medium-gray;
}
.ba-services-descriptions .column:nth-child(3n+1) {
	background-color: #dbdbdb;
}
.ba-service {
	text-align: center;
}
.ba-service p {
	font-style: italic;
	font-size: 18px;
	max-width: 215px;
	margin: 0 auto;
	margin-bottom: 0;
}
.ba-service__title {
	text-transform: uppercase;
	font-family: $body-font-family;
	font-weight: 700;
	font-size: 24px;
	color: $medium-gray;
	padding-top: 185px;



}
.ba-service__title--graphic {
	background: url(assets/img/icon-graphic.png) 50% 95px no-repeat;
}
.ba-service__title--web {
	background: url(assets/img/icon-web.png) 50% 95px no-repeat;
}
.ba-service__title--seo {
	background: url(assets/img/icon-seo.png) 50% 95px no-repeat;
}
.ba-service__title--mobile {
		background: url(assets/img/icon-mobile.png) 50% 95px no-repeat;
}
.ba-service p {
	padding-bottom: 125px;
}
// .ba-flex {
// 	display: flex;

// }
.ba-services-title {

	position: relative;
	background: url(assets/img/bg-services.jpg) 50% no-repeat;
	background-size: cover;
	z-index: 1;
	text-align: center;
	color: #FFF;
	padding: 0 25px;
}
.ba-services-title:before {
position: absolute;
content: "";
left: 0;
top: 0;
bottom: 0;
right: 0;
z-index: 0;
background: rgba(159,154,207,.4);
}
.ba-services-title__title {
	color: #FFF;
	position: relative;
	font-size: 35px;
	padding-top: 195px;
	background: url(assets/img/icon-services.png) 50% 110px no-repeat;
	padding-bottom: 100px;
}
.ba-services-title  .ba-services-title__text {
	font-size: 18px;
	font-style: italic;

	position: relative;
}
.ba-services-title .quote-author {
	color: #FFF;
	position: relative;
	padding-bottom: 335px;
	font-size: 18px;
	margin-bottom: 0;
}
.ba-works {
	background: url(assets/img/works-bg.jpg) 50% no-repeat;
	background-size: cover;
	position: relative;
	z-index: 1;
	color: #FFF;
	text-align: center;
}
.ba-works:before {
position: absolute;
content: "";
left: 0;
top: 0;
bottom: 0;
right: 0;
z-index: 0;
background: rgba(159,154,207,.8);
}
.ba-works__title {
	position: relative;
	padding-top: 140px;
	background: url(assets/img/icon-works.png) 50% 47px no-repeat;
	padding-bottom: 65px;
}
.ba-works-slider {
 position: relative;
 .slick-dots {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
  	padding-top: 45px;
   display: inline-block;

   button {
    font-size: 0;
    border: 2px solid #ebebeb;
    height: 10px;
    width: 10px;
    background: transparent;
    border-radius: 50%;
    margin: 3px;
    transition: background .3s;
   }
  }
  .slick-active {
   button {
    background: #ebebeb;
   }
  }
 }
}

.ba-works-prev{
	cursor: pointer;
	position: absolute;
	left: -250px;
	top: 250px;
	height: 40px;
	width: 30px;
	fill:#fff;
}
.ba-works-next{
	cursor: pointer;
	position: absolute;
	right: -250px;
	top: 250px;
	height: 40px;
	width: 30px;
	fill:#fff;
}
.ba-works .button {
	position: relative;
	color: #FFF;
	border-color: #FFF;
	margin-top: 45px;
	margin-bottom: 45px;

}

.ba-team {
	text-align: center;
}
.ba-team__title {
	padding-top: 140px;
	background: url(assets/img/icon-team.png) 50% 53px no-repeat;
	margin-bottom: 57px;
	color: $medium-gray;
	font-size: 36px;
}
