.ba-team-slider {
position: relative;
}
.ba-team-slide {
 position: relative;
 outline: none;
 padding-top: 195px;
 margin-right: 30px;
 &:last-child {
  margin-right: 0;
 }
 // padding-top: 195px;
 &__info {
  background: #FFF;
  padding-top: 160px;
  padding-bottom: 20px;
  position: relative;
 }
 img {
  position: absolute;
  top: -128px;
  left: 50%;
  margin-left: -130px;
 }
 &__title {
  color: $primary-color;
 }
 p {
  font-style: italic;
 }
}



.ba-team-slide-social {
	background-color: $primary-color;
	list-style: none;
	margin-left: 0;
}
.ba-team-slide-social__item {
	display: inline-block;
	padding: 15px 0;
}
.ba-team-slide-social-svg {
	height: 37px;
	width: 37px;
	 fill-rule: evenodd;
	fill: #FFF;
}
.ba-team-prev {
	position: absolute;
	top: 250px;
	left: -60px;
	height: 45px;
	width: 35px;
	fill:$medium-gray;
	&:hover {
		fill: map-get($foundation-palette, alert);
	}
}

.ba-team-next {
	position: absolute;
	top: 250px;
	right: -60px;
	height: 45px;
	width: 35px;
	fill:$medium-gray;
		&:hover {
		fill: map-get($foundation-palette, alert);
	}
}


.ba-team-slide-soc__link:hover .ba-team-slide-soc-svg{
	fill:$medium-gray;
}




.ba-map {
 height: 900px;
 position: relative;
 z-index: 0;
 &:before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  background: rgba(159,154,207,.5);
 }
}
.ba-contact {
 position: relative;

}
.ba-contact-content {
 padding: 55px 0;
 position: absolute;
 text-align: center;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;
 &__title {
  color: #FFF;
  padding-top: 90px;
  background: url(assets/img/map-pin.png) 50% 0 no-repeat;
 }
}
.ba-contact-form {
 padding: 260px 125px 0;

}
.ba-contact-form input,
.ba-contact-form textarea{
	&:focus {
		background-color: #FFF;
		boreder: 1px solid $primary-color;
	}
	position: relative;
	 background: rgba(215,213,224,.7);
	 boreder:none;
}
.button--form {
	position: relative;
	color: #FFF;
	background-color: $primary-color;
	&:hover {
		background-color: #FFF;
		color: $medium-gray;
	}
}
